import { _decorator, Component, Node, Button, Touch, EventTouch, NodeEventType, Vec2, v2 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Point')
export class Point extends Component {

    /**触摸点的位置 */
    private touchPos: Vec2 = v2(0, 0);

    onLoad() {
        this.addEvents();
    }

    onDestroy() {
        this.removeEvents();
    }

    start() {

    }

    private addEvents() {
        this.node.on(NodeEventType.TOUCH_MOVE, this.onTouchMove, this);
    }

    private removeEvents() {
        this.node.off(NodeEventType.TOUCH_MOVE, this.onTouchMove, this);
    }

    private onTouchMove(e: EventTouch) {
        console.log('touchMove:', e);
        e.touch.getUILocation(this.touchPos);
        this.node.setPosition(this.touchPos.x, this.touchPos.y);
    }

    update(deltaTime: number) {

    }
}

